<script>
import feather from 'feather-icons';
// import ProjectsFilter from './ProjectsFilter.vue';
import ProjectSingle from './ProjectSingle.vue';
import projects from '../../data/projects';

export default {
	components: { 
		ProjectSingle, 
		// ProjectsFilter 
	},
	data: () => {
		return {
			projects,
			projectsHeading: 'Projects',
			selectedCategory: 'All',
			searchProject: '',
			no: false
		};
	},
	created() {
		
	},
	computed: {
		// Get the filtered projects
		filteredProjects() {
			if (this.selectedCategory === 'All') {
				// console.log(this.projects);
				return this.projects.slice(0,6);
			}
			if (this.selectedCategory) {
				return this.filterProjectsByCategory();
			} else if (this.searchProject) {
				return this.filterProjectsBySearch();
			}
			return this.projects;
		},
	},
	methods: {
		// Filter projects by category
		filterProjectsByCategory() {
			if(this.selectedCategory === 'All'){
				return this.projects;
			}
			else {
				return this.projects.filter((item) => {
					let category =
						item.category.charAt(0).toUpperCase() +
						item.category.slice(1);
					// console.log(category);
					return category.includes(this.selectedCategory);
				});
			} 
			
		},
		// Filter projects by title search
		filterProjectsBySearch() {
			let project = new RegExp(this.searchProject, 'i');
			return this.projects.filter((el) => el.title.match(project));
		},
		selectProjectCategory(category) {
			this.selectedCategory = category;

			var cols = document.getElementsByTagName('button');
				for(var i = 0; i < cols.length; i++) {
					cols[i].style.borderWidth = '1px';
			}


			var selectedButton;
			if(category==='All'){
				selectedButton = document.getElementById('all-button');
				selectedButton.style.borderWidth = '3px';
				// selectedButton.style.borderColor = 
			}
			else if(category==='Hardware'){
				selectedButton = document.getElementById('hardware-button');
				selectedButton.style.borderWidth = '3px';
			}
			else if(category==='Networks'){
				selectedButton = document.getElementById('networks-button');
				selectedButton.style.borderWidth = '3px';
			}
			else if(category==='OS'){
				selectedButton = document.getElementById('os-button');
				selectedButton.style.borderWidth = '3px';
			}
			else if(category==='Full-Stack'){
				selectedButton = document.getElementById('full-stack-button');
				selectedButton.style.borderWidth = '3px';
			}
			else if(category==='Privacy'){
				selectedButton = document.getElementById('privacy-button');
				selectedButton.style.borderWidth = '3px';
			}
			else if(category==='Machine Learning'){
				selectedButton = document.getElementById('machine-learning-button');
				selectedButton.style.borderWidth = '3px';
			}
		}
	},
	mounted() {
		feather.replace();
		this.selectProjectCategory('All');
	},
};
</script>

